import React, { useState, useContext } from "react";
import "./App.css";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import Home from "./Home.js";
import "semantic-ui-css/semantic.min.css";
import userData from "./employee.json";
import Add from "./images/fondo.jpg";
import {
  Header,
  Menu,
  Icon,
  Modal,
  Button,
  Input,
  Segment,
  Checkbox,
  Sidebar,
  Dropdown,
} from "semantic-ui-react";
import Observacion from "./observacion";
import Tareas from "./tareas";
import Consultas from "./consulta";
import Registrar from "./registrar";
import Categorias from "./tipos";
import Login from "./Login";
import Reporte from "./Reporte";
import { Contexto } from "./Contexto";

function App() {
  let iniciado = null;
  let caption = "Sign In";
  if (sessionStorage.getItem("user")) {
    iniciado = sessionStorage.user;
    caption = "Sign Out";
  } else if (localStorage.getItem("user")) {
    iniciado = localStorage.user;
    caption = "Sign Out";
  }
  const {
    ["user"]: [user, setUser],
  } = useContext(Contexto);
  const [width, setWidth] = React.useState(window.innerWidth);
  const breakpoint = 620;
  const [session, setSession] = useState(caption);
  const [open, setOpen] = React.useState(false);
  const [password, setPassword] = useState("");
  const [activeItem, setActiveItem] = useState("");
  const [recordar, setRecordar] = useState(false);
  const [visible, setVisible] = useState(false);

  const handleItemClick = (e, { name }) => {
    setActiveItem(name);
  };
  const onChangeHandler = (event) => {
    const { name, value } = event.currentTarget;

    if (name === "userPassword") {
      setPassword(value);
    }
  };

  const login = (event, password) => {
    event.preventDefault();
    var found = false;
    for (var i = 0; i < userData.length; i++) {
      if (userData[i].id === password) {
        found = true;
        setUser(userData[i].Nombre);
        sessionStorage.user = userData[i].Nombre;
        if (recordar) {
          localStorage.user = userData[i].Nombre;
        }
        setSession("Sign Out");
        break;
      }
    }
    if (!found) alert(`Usuario No encontrado`);
  };

  React.useEffect(() => {
    const handleWindowResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleWindowResize);

    // Return a function from the effect that removes the event listener
    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  return (
    <Router basename={"/"}>
      <div style={{ padding: "2%", backgroundImage: `url(${Add})` }}>
        <Menu color="red" size="huge">
          <Menu.Item header as={Link} to="/home">
            <b style={{ color: "red" }}>Mi Cultura QSEB</b>
          </Menu.Item>
          {width < breakpoint ? (
            <Menu.Item position="right">
              <Icon
                size="large"
                name="bars"
                color="red"
                onClick={() => {
                  setVisible(true);
                }}
              />
            </Menu.Item>
          ) : (
            <>
              <Dropdown item text="Reportes">
                <Dropdown.Menu>
                  <Dropdown.Item
                    text="Reporte de Turno"
                    as={Link}
                    to={user ? "/Reporte" : "/Login"}
                  />
                  <Dropdown.Item text="Recorridos" />
                  <Dropdown.Item text="Actos Inseguros" />
                  <Dropdown.Item text="Condiciones Inseguras" />
                </Dropdown.Menu>
              </Dropdown>
              <Menu.Item name="Noticias">Noticias</Menu.Item>
              <Menu.Item position="right">
                {user ? (
                  <Icon
                    size="large"
                    name="user circle"
                    color={user ? "green" : "black"}
                  />
                ) : (
                  <Button
                    content="Iniciar Sesion"
                    style={{ backgroundColor: "#F44336", color: "white" }}
                    as="a"
                    href="/Login"
                  />
                )}
              </Menu.Item>
            </>
          )}
        </Menu>
        <Sidebar.Pushable as={Segment}>
          <Sidebar
            as={Menu}
            animation="overlay"
            icon="labeled"
            onHide={() => setVisible(false)}
            vertical
            direction="right"
            visible={visible}
            width="wide"
            color="red"
          >
            <Menu.Item
              name="Reporte"
              as={Link}
              to={user ? "/Reporte" : "/Login"}
              onClick={() => {
                setVisible(false);
              }}
            >
              Reporte de Turno
            </Menu.Item>
            <Menu.Item
              name="Recorridos"
              as={Link}
              to={user ? "/registros" : "/Login"}
              onClick={() => {
                setVisible(false);
              }}
            >
              Recorridos
            </Menu.Item>
            <Menu.Item name="ActosI">Actos Inseguros</Menu.Item>
            <Menu.Item name="CondicionesI">Condiciones Inseguras</Menu.Item>
            <Menu.Item name="Noticias">Noticias</Menu.Item>
            <Menu.Item>
              {user ? (
                user
              ) : (
                <Button
                  content="Iniciar Sesion"
                  style={{ backgroundColor: "#F44336", color: "white" }}
                  as={Link}
                  to="/Login"
                />
              )}
            </Menu.Item>
          </Sidebar>

          <Sidebar.Pusher>
            <Segment
              style={{
                backgroundColor: "whitesmoke",
                height: "75vh",
                overflowY: "scroll",
              }}
            >
              <Switch>
                <Route
                  exact
                  path="/home"
                  render={(props) => <Home {...props} user={user} />}
                />
                <Route
                  path="/registros"
                  render={(props) => <Registrar {...props} user={user} />}
                />
                <Route
                  path="/tareas"
                  render={(props) => <Tareas {...props} user={user} />}
                />
                <Route
                  path="/observaciones"
                  render={(props) => <Consultas {...props} user={user} />}
                />
                <Route
                  path="/observacion/:id"
                  render={(props) => <Observacion {...props} user={user} />}
                />
                <Route
                  path="/Login"
                  render={(props) => <Login {...props} user={user} />}
                />
                <Route
                  path="/Reporte"
                  render={(props) => <Reporte {...props} user={user} />}
                />
                <Route
                  path="/"
                  render={(props) => <Home {...props} user={user} />}
                />
              </Switch>
            </Segment>
            <Menu
              fluid
              widths={5}
              icon="labeled"
              inverted
              style={{ fontSize: 10 }}
            >
              <Menu.Item
                name="estadistica"
                active={activeItem === "estadistica"}
                onClick={handleItemClick}
                as={Link}
                to="/home"
              >
                <Icon name="chart line" />
                Estadisticas
              </Menu.Item>
              <Menu.Item
                name="observaciones"
                active={activeItem === "observaciones"}
                onClick={handleItemClick}
                as={Link}
                to="/observaciones"
              >
                <Icon name="search" />
                Observaciones
              </Menu.Item>
              <Menu.Item
                name="registrar"
                active={activeItem === "registrar"}
                onClick={handleItemClick}
                as={Link}
                to="/registros"
              >
                <Icon name="add square" />
                Registrar
              </Menu.Item>
              <Menu.Item
                name="tareas"
                active={activeItem === "tareas"}
                onClick={handleItemClick}
                as={Link}
                to="/tareas"
              >
                <Icon name="tasks" />
                Tareas
              </Menu.Item>
              <Modal
                basic
                onClose={() => {
                  setOpen(false);
                }}
                onOpen={() => setOpen(true)}
                open={open}
                size="large"
                trigger={
                  <Menu.Item
                    name="usuario"
                    active={activeItem === "usuario"}
                    onClick={handleItemClick}
                  >
                    <Icon name="user circle" color={user ? "green" : "red"} />
                    {user ? user : "Usuario"}
                  </Menu.Item>
                }
              >
                {!user ? (
                  <Segment textAlign="center">
                    <Header icon>
                      <Icon name="key" />
                      Sign In
                    </Header>
                    <Header as="h3">
                      <Modal.Content>
                        <Input iconPosition="left" placeholder="Password">
                          <Icon name="key" />
                          <input
                            name="userPassword"
                            type="password"
                            onChange={(event) => onChangeHandler(event)}
                          />
                        </Input>
                        <Modal.Description>
                          <p>
                            <Checkbox
                              label="Recordar Usuario"
                              onChange={(e, { checked }) => {
                                checked
                                  ? setRecordar(true)
                                  : setRecordar(false);
                              }}
                            />
                          </p>
                        </Modal.Description>
                      </Modal.Content>
                    </Header>
                    <Modal.Actions>
                      <Button
                        color="red"
                        inverted
                        onClick={() => setOpen(false)}
                      >
                        <Icon name="remove" /> Cancel
                      </Button>
                      <Button
                        color="green"
                        inverted
                        onClick={(event) => {
                          login(event, password);
                          setOpen(false);
                        }}
                      >
                        <Icon name="checkmark" /> Sign In
                      </Button>
                    </Modal.Actions>
                  </Segment>
                ) : (
                  <Segment
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                    }}
                    textAlign="center"
                  >
                    <Menu inverted vertical>
                      <Menu.Item
                        name="contact"
                        active={activeItem === "contact"}
                        as="a"
                        href="https://wa.me/50769493169?text=Hola%20Chino"
                      />
                      <Menu.Item
                        name={session}
                        onClick={() => {
                          setUser("");
                          sessionStorage.removeItem("user");
                          localStorage.removeItem("user");
                          iniciado = null;
                          setSession("Sign In");
                          setOpen(false);
                        }}
                      />
                    </Menu>
                  </Segment>
                )}
              </Modal>
            </Menu>
          </Sidebar.Pusher>
        </Sidebar.Pushable>
      </div>
    </Router>
  );
}

export default App;
