import React, { useState, useContext, useEffect } from "react";
import "./App.css";
import { useHistory } from "react-router-dom";
import "semantic-ui-css/semantic.min.css";
import { storage } from "./firebase/firebase";
import {
  List,
  Button,
  Image,
  Checkbox,
  Dimmer,
  Loader,
  Segment,
  Header,
  Label,
  Grid,
  Icon,
  Modal,
  Divider,
  Input,
  Form,
  TextArea,
  Dropdown,
  Progress,
  Container,
} from "semantic-ui-react";
import { Contexto } from "./Contexto";
import { GoogleSpreadsheet } from "google-spreadsheet";
import imageCompression from "browser-image-compression";

function ExcelDateToJSDate(serial) {
  var utc_days = Math.floor(serial - 25568);
  var utc_value = utc_days * 86400;
  var date_info = new Date(utc_value * 1000);

  var fractional_day = serial - Math.floor(serial) + 0.0000001;

  var total_seconds = Math.floor(86400 * fractional_day);

  var seconds = total_seconds % 60;

  total_seconds -= seconds;

  var hours = Math.floor(total_seconds / (60 * 60));
  var minutes = Math.floor(total_seconds / 60) % 60;

  var date_data = new Date(
    date_info.getFullYear(),
    date_info.getMonth(),
    date_info.getDate(),
    hours,
    minutes,
    seconds
  );

  return date_data;
}

function FechaStringShort(fecha) {
  var ye = new Intl.DateTimeFormat("en", { year: "numeric" }).format(fecha);
  var mo = new Intl.DateTimeFormat("en", { month: "numeric" }).format(fecha);
  var da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(fecha);
  var fechaStr = `${ye}-${mo < 10 ? "0" + mo : mo}-${da}`;
  return fechaStr;
}

export default function Reporte({ user }) {
  const {
    ["reportData"]: [reportData, setRepData],
  } = useContext(Contexto);
  const {
    ["areas"]: [areas, setAreas],
  } = useContext(Contexto);
  const [area, setArea] = useState();
  const { ["personal"]: personal } = useContext(Contexto);
  const [reportes, setReportes] = useState();
  const [fecha, setFecha] = useState(FechaStringShort(new Date()));
  const [saving, setSaving] = useState(false);
  const [requerimientos, setReq] = useState();
  const [activeReq, setActiveR] = useState();
  const [incumple, setIncumple] = useState([]);
  const [percentage, setPercentage] = useState();
  const [turno, setTurno] = useState(1);
  const [confirmar, setConfirmar] = useState(false);
  const [duplicado, setDuplicado] = useState(false);
  const [listaArea, setListaArea] = useState();
  const dummyImg = "https://react.semantic-ui.com/images/wireframe/image.png";
  const [previewImg, setPreviewImg] = useState(dummyImg);
  const [imageAsFile, setImageAsFile] = useState("");
  async function handleImageUpload(event, rubro) {
    if (event.target.files && event.target.files[0]) {
      const imageFile = event.target.files[0];
      delete incumple[incumple.findIndex((x) => x.rubro === rubro)].url;
      delete incumple[incumple.findIndex((x) => x.rubro === rubro)].preview;
      delete incumple[incumple.findIndex((x) => x.rubro === rubro)].img;
      console.log("originalFile instanceof Blob", imageFile instanceof Blob); // true
      console.log(`originalFile size ${imageFile.size / 1024 / 1024} MB`);
      var reader = new FileReader();
      reader.onload = function (e) {
        setPreviewImg(e.target.result);
        incumple[incumple.findIndex((x) => x.rubro === rubro)].preview =
          e.target.result;
      };
      reader.readAsDataURL(event.target.files[0]);
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true,
      };
      try {
        const compressedFile = await imageCompression(imageFile, options);
        console.log(
          "compressedFile instanceof Blob",
          compressedFile instanceof Blob
        ); // true
        console.log(
          `compressedFile size ${compressedFile.size / 1024 / 1024} MB`
        ); // smaller than maxSizeMB
        setImageAsFile((imageFile) => compressedFile);
        incumple[incumple.findIndex((x) => x.rubro === rubro)].img =
          compressedFile;
        var boton = document.getElementById("btn" + rubro);
        boton.disabled = false;
      } catch (error) {
        console.log(error);
      }
    } else {
      setImageAsFile("");
      setPreviewImg(dummyImg);
      delete incumple[incumple.findIndex((x) => x.rubro === rubro)].preview;
    }
    setPercentage(0);
  }

  async function handleFireBaseUpload(rubro) {
    var boton = document.getElementById("btn" + rubro);
    boton.disabled = true;
    setPercentage(1);
    const archivo = incumple[incumple.findIndex((x) => x.rubro === rubro)].img;
    const uploadTask = storage.ref(`/images/${archivo.name}`).put(archivo);
    setPercentage(5);
    uploadTask.on(
      "state_changed",
      (snapShot) => {
        //takes a snap shot of the process as it is happening
        console.log(snapShot);
      },
      (err) => {
        //catches the errors
        console.log(err);
      },
      () => {
        // gets the functions from storage refences the image storage in firebase by the children
        // gets the download url then sets the image from firebase as the value for the imgUrl key:
        setPercentage(50);
        storage
          .ref("images")
          .child(archivo.name)
          .getDownloadURL()
          .then((fireBaseUrl) => {
            incumple[incumple.findIndex((x) => x.rubro === rubro)].url =
              fireBaseUrl;

            setPercentage(100);
          });
      }
    );
  }

  const sacardata = async () => {
    var data = [];
    let datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[474202802];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;

    let lastr = rows.length + 1;
    var j, i;
    //await sheet.loadCells("A1:V" + lastr.toString());
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setRepData(datas);
  };
  const sacarReportes = async () => {
    var data = [];
    let datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[876764799];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;

    let lastr = rows.length + 1;
    var j, i;
    //await sheet.loadCells("A1:V" + lastr.toString());
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setReportes(datas);
  };
  async function registrar() {
    setSaving(true);
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[876764799];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    let lastr = rows.length;
    while (lastr--) {
      try {
        console.log(rows[lastr].Usuario);
        if (
          rows[lastr].Usuario == user &&
          rows[lastr].Turno == turno &&
          rows[lastr].Area == area &&
          rows[lastr].Fecha == FechaStringShort(new Date())
        ) {
          console.log("Borrando");
          await rows[lastr].delete();
        }
      } catch (err) {
        console.log(err);
        break;
      }
    }
    await sheet.addRow({
      Usuario: user,
      Fecha: FechaStringShort(new Date()),
      Turno: turno,
      Area: area,
    });
    if (incumple.length > 0) {
      var j;
      for (j = 0; j < incumple.length; j++) {
        var personas;
        incumple[j].involucrados
          ? (personas = incumple[j].involucrados.toString())
          : (personas = "");
        await sheet.addRow({
          Usuario: user,
          Fecha: FechaStringShort(new Date()),
          Turno: turno,
          Requerimiento: incumple[j].req,
          Rubro: incumple[j].rubro,
          Detalle: incumple[j].detalle,
          Involucrados: personas,
          Imagen: incumple[j].url,
          Area: area,
        });
      }
    }
    alert("Datos Guardados");
    setIncumple([]);
    setActiveR();
    setSaving(false);
    setReportes();
    setArea("");
    setConfirmar(false);
  }
  const [lista, setLista] = useState();
  const crearLista = (e, { name }) => {
    if (activeReq == name) {
      setActiveR();
    } else {
      setActiveR(name);
    }
    setLista(
      reportData
        .filter((item) => item.Nombre == name)
        .map((item) => item.Rubro)
        .filter((value, index, self) => self.indexOf(value) === index)
    );
  };
  const revision = (e, { name, requerimiento }) => {
    let observacion = { req: requerimiento, rubro: name };
    if (incumple.some((e) => e.req === requerimiento && e.rubro === name)) {
      let removeIndex = incumple.filter((e) => e.rubro !== name);
      console.log(removeIndex);
      // remove object
      setIncumple(removeIndex);
    } else {
      setIncumple((oldArray) => [...oldArray, observacion]);
    }
  };
  const validar = () => {
    let res = [];
    console.log(reportes);
    res = reportes.filter((element) => {
      return (
        element.Usuario === user &&
        element.Turno == turno &&
        element.Area === area &&
        FechaStringShort(ExcelDateToJSDate(element.Fecha)) === fecha
      );
    });
    if (res.length > 0) {
      setDuplicado(true);
    } else {
      setDuplicado(false);
    }
  };

  const sacarAreas = async () => {
    var data = [];
    let datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[1705686078];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;

    let lastr = rows.length + 1;
    var j, i;
    //await sheet.loadCells("A1:V" + lastr.toString());
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setAreas(datas);
  };

  useEffect(() => {
    if (!areas) {
      sacarAreas();
    } else {
      var j;
      var lastr = areas.length;
      const area = [];
      for (j = 0; j < lastr; j++) {
        if (areas[j].name !== "") {
          area.push({
            key: areas[j].name,
            text: areas[j].name,
            value: areas[j].name,
          });
        }
      }
      setListaArea(area);
    }
  }, [areas]);

  useEffect(() => {
    console.log(incumple);
  }, [incumple]);
  useEffect(() => {
    if (!reportData) {
      sacardata();
    } else {
      setReq(
        reportData
          .map((item) => item.Nombre)
          .filter((value, index, self) => self.indexOf(value) === index)
      );
    }
  }, [reportData]);
  useEffect(() => {
    if (!reportes) {
      sacarReportes();
    }
  }, [reportes]);
  return (
    <div style={{ alignItems: "center" }}>
      <Modal
        onClose={() => setConfirmar(false)}
        onOpen={() => setConfirmar(true)}
        size="large"
        open={confirmar}
      >
        <Modal.Header>Confirmar Informacin</Modal.Header>
        <Modal.Content>
          <h1>Reporte de Turno {turno}</h1>
          <h2>{fecha}</h2>
          <h3>{user}</h3>
          <h4>{area}</h4>
          {duplicado && (
            <Label attached="top" color="red">
              <h4>
                Ud. ya ha registrado Reporte de Turno {turno} del rea de {area}{" "}
                para la fecha seleccionada, si continua se borrarn los datos
                previos.
              </h4>
            </Label>
          )}
        </Modal.Content>

        <Divider />
        <Modal.Content>
          {incumple.length > 0 && <b>Incumplimientos: </b>}
          {incumple.length > 0 && (
            <List>
              {incumple.map((item, index) => {
                return (
                  <List.Item>
                    {index + 1}. {item.req} - {item.rubro}
                  </List.Item>
                );
              })}
            </List>
          )}
          {incumple.length === 0 && <b>Sin Incidencias</b>}
        </Modal.Content>
        <Modal.Actions>
          <Button
            color="green"
            onClick={() => {
              registrar();
            }}
          >
            <Icon name="check"></Icon>Confirmar
          </Button>
          <Button
            color="red"
            onClick={() => {
              setConfirmar(false);
            }}
          >
            <Icon name="x"></Icon>Cancelar
          </Button>
        </Modal.Actions>
      </Modal>
      <Dimmer active={!reportData || saving || !reportes} page>
        <Loader>{saving ? "Saving" : "Loading"}</Loader>
      </Dimmer>
      <Grid columns={1}>
        <Grid.Column>
          <Segment>
            <Label attached="top" color="red">
              <Input
                type="date"
                size="tiny"
                defaultValue={fecha}
                onChange={(e, { value }) => {
                  setFecha(value);
                }}
              ></Input>
            </Label>
          </Segment>
          <Divider />
          <Segment>
            <Header as="h2">
              Reporte de
              <Grid columns={4}>
                <Grid.Column>Turno</Grid.Column>
                {[...Array(3).keys()].map((i) => (
                  <Grid.Column>
                    <Checkbox
                      radio
                      label={i + 1}
                      checked={turno === i + 1}
                      onChange={() => {
                        setTurno(i + 1);
                      }}
                    />
                  </Grid.Column>
                ))}
              </Grid>
              <Label
                attached="top right"
                color={incumple.length > 0 ? "red" : "grey"}
              >
                Incumpliento{incumple.length > 1 ? "s" : ""} {incumple.length}
              </Label>
            </Header>
          </Segment>
          <Grid.Row>
            <Segment basic>
              <Header content={"Area"} />
              <Dropdown
                placeholder="Area Supervisada.."
                fluid
                selection
                search
                scrolling
                clearable
                value={area}
                options={listaArea}
                onChange={(e, { value }) => {
                  setArea(value);
                }}
              />
            </Segment>
          </Grid.Row>
        </Grid.Column>
      </Grid>
      <Segment basic>
        <Button
          fluid
          size="big"
          disabled={!area}
          color="green"
          onClick={() => {
            validar();
            setConfirmar(true);
          }}
        >
          <Icon name="save"></Icon>
          Guardar
        </Button>
      </Segment>
      {area && (
        <Segment basic>
          <Header content={"Requerimientos"} />
          <List divided verticalAlign="middle">
            {reportData &&
              requerimientos &&
              requerimientos.map((item) => {
                return (
                  <List.Item>
                    <List.Content>
                      <Button
                        circular
                        name={item}
                        fluid
                        style={{
                          backgroundImage:
                            "linear-gradient(to right,#B0140E,#F44336)",
                          color: "white",
                        }}
                        size="big"
                        onClick={crearLista}
                      >
                        {item} <Icon name="caret square down outline"></Icon>
                      </Button>
                      {activeReq === item && (
                        <Segment basic>
                          <Header content={"Rubros"} />
                          *quitar gancho a los que no cumplen.*{" "}
                          {lista &&
                            activeReq == item &&
                            lista.map((list) => {
                              return (
                                <Segment
                                  basic
                                  onClick={() => {
                                    console.log(incumple);
                                  }}
                                >
                                  <Checkbox
                                    id={list}
                                    name={list}
                                    actos={
                                      reportData[
                                        reportData.findIndex(
                                          (x) => x.Rubro === list
                                        )
                                      ].Acto
                                    }
                                    requerimiento={activeReq}
                                    checked={
                                      incumple.filter(
                                        (item) =>
                                          item.req === activeReq &&
                                          item.rubro === list
                                      ).length > 0
                                        ? false
                                        : true
                                    }
                                    label={list}
                                    onChange={revision}
                                  />
                                  {incumple.filter(
                                    (item) =>
                                      item.req === activeReq &&
                                      item.rubro === list
                                  ).length > 0 && (
                                    <Segment>
                                      <Grid columns={1}>
                                        <Grid.Column>
                                          <Form>
                                            <TextArea
                                              placeholder="Detalle..."
                                              defaultValue={
                                                incumple[
                                                  incumple.findIndex(
                                                    (x) => x.rubro === list
                                                  )
                                                ].detalle
                                                  ? incumple[
                                                      incumple.findIndex(
                                                        (x) => x.rubro === list
                                                      )
                                                    ].detalle
                                                  : ""
                                              }
                                              onChange={(e, { value }) => {
                                                incumple[
                                                  incumple.findIndex(
                                                    (x) => x.rubro === list
                                                  )
                                                ].detalle = value;
                                              }}
                                            />
                                          </Form>
                                        </Grid.Column>

                                        {reportData[
                                          reportData.findIndex(
                                            (x) => x.Rubro === list
                                          )
                                        ].Acto === "Y" && (
                                          <Grid.Column>
                                            <Dropdown
                                              fluid
                                              multiple
                                              search
                                              selection
                                              defaultValue={
                                                incumple[
                                                  incumple.findIndex(
                                                    (x) => x.rubro === list
                                                  )
                                                ].involucrados
                                                  ? incumple[
                                                      incumple.findIndex(
                                                        (x) => x.rubro === list
                                                      )
                                                    ].involucrados
                                                  : ""
                                              }
                                              placeholder="Seleccionar Personas Involucradas"
                                              options={personal}
                                              onChange={(e, { value }) => {
                                                incumple[
                                                  incumple.findIndex(
                                                    (x) => x.rubro === list
                                                  )
                                                ].involucrados = value;
                                              }}
                                            ></Dropdown>
                                          </Grid.Column>
                                        )}
                                        <Grid.Column>
                                          <Input
                                            icon="camera"
                                            iconPosition="left"
                                            type="file"
                                            fluid
                                            rubro={list}
                                            onChange={(e, { rubro }) => {
                                              handleImageUpload(e, rubro);
                                            }}
                                          />
                                        </Grid.Column>
                                        {incumple[
                                          incumple.findIndex(
                                            (x) => x.rubro === list
                                          )
                                        ].preview && (
                                          <Grid columns={2}>
                                            <Grid.Column>
                                              <Image
                                                src={
                                                  incumple[
                                                    incumple.findIndex(
                                                      (x) => x.rubro === list
                                                    )
                                                  ].preview
                                                }
                                                size="small"
                                              />
                                            </Grid.Column>
                                            <Grid.Column>
                                              <Button
                                                color="blue"
                                                id={"btn" + list}
                                                icon
                                                size="mini"
                                                labelPosition="left"
                                                rubro={list}
                                                onClick={(e, { rubro }) => {
                                                  handleFireBaseUpload(rubro);
                                                }}
                                              >
                                                <Icon name="upload" />
                                                Confirmar
                                              </Button>
                                              <Progress
                                                percent={
                                                  incumple[
                                                    incumple.findIndex(
                                                      (x) => x.rubro === list
                                                    )
                                                  ].url
                                                    ? 100
                                                    : percentage
                                                }
                                                indicating={
                                                  !incumple[
                                                    incumple.findIndex(
                                                      (x) => x.rubro === list
                                                    )
                                                  ].url && percentage != 100
                                                }
                                                success={
                                                  incumple[
                                                    incumple.findIndex(
                                                      (x) => x.rubro === list
                                                    )
                                                  ].url
                                                    ? true
                                                    : percentage == 100
                                                }
                                              />
                                            </Grid.Column>
                                          </Grid>
                                        )}
                                      </Grid>
                                    </Segment>
                                  )}
                                </Segment>
                              );
                            })}
                        </Segment>
                      )}
                    </List.Content>
                  </List.Item>
                );
              })}
          </List>
        </Segment>
      )}
    </div>
  );
}
