import React, { useState, useEffect, useRef } from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import emailjs from "emailjs-com";
import apiKeys from "./apikeys";
import { storage } from "./firebase/firebase";
import { useHistory } from "react-router-dom";
import {
  Container,
  Button,
  Popup,
  Image,
  Input,
  Icon,
  TextArea,
  Form,
  Segment,
  Dropdown,
  Step,
  Header,
  Card,
  Rating,
  Ref,
  Dimmer,
  Loader,
  Modal,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";
import "react-datepicker/dist/react-datepicker.css";
import imageCompression from "browser-image-compression";
import userData from "./employee.json";

function Categorias({ user }) {
  //Fechas
  let today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1;
  var yyyy = today.getFullYear();
  var hora = today.getHours();
  var minutos = today.getMinutes();
  dd < 10 ? (dd = "0" + dd) : (dd = today.getDate());
  mm < 10 ? (mm = "0" + mm) : (mm = today.getMonth() + 1);
  if (hora < 10) hora = "0" + hora;
  if (minutos < 10) minutos = "0" + minutos;
  let date = yyyy + "-" + mm + "-" + dd;
  let fecha = date + " " + hora + ":" + minutos;
  //Fechas
  const dummyImg = "https://react.semantic-ui.com/images/wireframe/image.png";
  const [loading, setLoading] = useState(true);
  const [perfil, setPerfil] = useState();
  const [correo, setCorreo] = useState("No Aplica");
  const [creador, setCreador] = useState();
  const [step, setStep] = useState("Que");
  const [tipo, setTipo] = useState("");
  const [cat, setCat] = useState("");
  const [data, setData] = useState("");
  const [data2, setData2] = useState("");
  const [catData, setCatData] = useState("");
  const [subCatData, setSubCatData] = useState("");
  const [subCat, setSubCat] = useState("");
  const [detalles, setDetalles] = useState("");
  const [area, setArea] = useState("");
  const [que, setQue] = useState(false);
  const [donde, setDonde] = useState(false);
  const [pacStep, setPacStep] = useState(false);
  const [involucrados, setInvolucrados] = useState([]);
  const [plan, setPlan] = useState("");
  const [startDate, setStartDate] = useState(date);
  const [resp, setResp] = useState("");
  const [saving, setSaving] = useState(false);
  const segCat = useRef(null);
  const segSubCat = useRef(null);
  const allInputs = { imgUrl: "" };
  const [previewImg, setPreviewImg] = useState(dummyImg);
  const [imageAsFile, setImageAsFile] = useState("");
  const [imageAsUrl, setImageAsUrl] = useState(allInputs);
  const [subir, setSubir] = useState(false);
  const [guardar, setGuardar] = useState(false);
  const [confirmar, setConfirmar] = useState(false);
  const [severidad, setSeveridad] = useState(1);

  const history = useHistory();

  const tipos = [
    { id: "Q", name: "Calidad" },
    { id: "S", name: "Seguridad" },
    { id: "E", name: "Ambiente" },
    { id: "B", name: "Bioseguridad" },
  ];

  const areas = [
    { id: 2, name: "Linea1", short: "L1" },
    { id: 3, name: "Linea2", short: "L2" },
    { id: 4, name: "Linea3", short: "L3" },
    { id: 5, name: "Linea4", short: "L4" },
    { id: 6, name: "Linea5", short: "L5" },
    { id: 1, name: "Area de Reciclaje", short: "AR" },
    { id: 8, name: "Sala de Jarabe", short: "SJ" },
    { id: 9, name: "Taller de Mtto", short: "TM" },
    { id: 9, name: "Area de Operaciones", short: "OP" },
    { id: 7, name: "PTAR", short: "PTAR" },
    { id: 10, name: "PTA", short: "PTA" },
    { id: 11, name: "Oficina Administrativa", short: "ADM" },
  ];

  async function handleImageUpload(event) {
    if (event.target.files && event.target.files[0]) {
      const imageFile = event.target.files[0];
      console.log("originalFile instanceof Blob", imageFile instanceof Blob); // true
      console.log(`originalFile size ${imageFile.size / 1024 / 1024} MB`);
      var reader = new FileReader();
      var preview = document.querySelector("#blah");
      console.log(preview);
      reader.onload = function (e) {
        setPreviewImg(e.target.result);
      };

      reader.readAsDataURL(event.target.files[0]);
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true,
      };
      try {
        const compressedFile = await imageCompression(imageFile, options);
        console.log(
          "compressedFile instanceof Blob",
          compressedFile instanceof Blob
        ); // true
        console.log(
          `compressedFile size ${compressedFile.size / 1024 / 1024} MB`
        ); // smaller than maxSizeMB
        setImageAsFile((imageFile) => compressedFile);
      } catch (error) {
        console.log(error);
      }
      setSubir(true);
    } else {
      setSubir(false);
      setImageAsFile("");
      setImageAsUrl("");
      setPreviewImg(dummyImg);
      var archivoSelect = document.querySelector("#anexoImg");
      console.log(archivoSelect);
      archivoSelect.value = "";
    }
  }

  async function handleFireBaseUpload() {
    if (subir) {
      console.log("start of upload");
      console.log(imageAsFile);
      if (imageAsFile === "") {
        console.error(
          `not an image, the image file is a ${typeof imageAsFile}`
        );
      } else {
        const uploadTask = storage
          .ref(`/iconos/${imageAsFile.name}`)
          .put(imageAsFile);
        uploadTask.on(
          "state_changed",
          (snapShot) => {
            //takes a snap shot of the process as it is happening
            console.log(snapShot);
          },
          (err) => {
            //catches the errors
            console.log(err);
          },
          () => {
            // gets the functions from storage refences the image storage in firebase by the children
            // gets the download url then sets the image from firebase as the value for the imgUrl key:
            storage
              .ref("iconos")
              .child(imageAsFile.name)
              .getDownloadURL()
              .then((fireBaseUrl) => {
                setImageAsUrl((prevObject) => ({
                  ...prevObject,
                  imgUrl: fireBaseUrl,
                }));
              });
          }
        );
      }
    }
  }

  var sheetNo = 2125477317;
  var sheetNo2 = 715064374;

  const sacardata = async () => {
    var datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[sheetNo];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;

    let lastr = rows.length + 1;
    var j, i;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      var data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setData(datas);
    console.log(datas);
    var datas2 = [];
    const sheet2 = doc.sheetsById[sheetNo2];
    const rows2 = await sheet2.getRows();
    lastr = rows2.length + 1;
    await sheet2.loadCells("A1:C" + lastr.toString());
    for (j = 1; j < lastr; j++) {
      const ar = sheet2.getCell(j, 0);
      const br = sheet2.getCell(j, 1);
      const cr = sheet2.getCell(j, 2);
      datas2.push({
        Tipo: ar.value,
        Categoria: br.value,
        SubCategoria: cr.value,
        key: cr.value,
        text: cr.value,
        value: cr.value,
      });
    }
    setData2(datas2);
  };

  var j;
  var lastr = userData.length;
  const personal = [];
  for (j = 0; j < lastr; j++) {
    if (userData[j].Nombre !== "") {
      personal.push({
        key: userData[j].Nombre,
        text: userData[j].Nombre,
        value: userData[j].Nombre,
      });
    }
  }
  useEffect(() => {
    if (user) {
      const datosUsuario = userData.filter((item) => {
        return item.Nombre === user;
      });
      setPerfil(datosUsuario[0].Perfil);
      setCreador(user);
    }
  }, [user]);

  useEffect(() => {
    if (imageAsUrl) {
      console.log(imageAsUrl.imgUrl);
    }
  }, [imageAsUrl]);

  useEffect(() => {
    if (resp) {
      const datoResp = userData.filter((item) => {
        return item.Nombre === resp;
      });
      setCorreo(datoResp[0].correo);
    }
  }, [resp]);

  async function registrar(tipo, categoria) {
    handleFireBaseUpload();
    console.log(imageAsUrl.imgUrl);
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[sheetNo];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    let lastr = rows.length + 1;
    var j;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      if (
        sheet.getCell(j, 0).value === tipo &&
        sheet.getCell(j, 1).value === categoria
      ) {
        const imagen = sheet.getCell(j, 4);
        imagen.value = imageAsUrl.imgUrl;
        await sheet.saveUpdatedCells();
        break;
      }
    }
  }

  function notificar() {
    var templateParams = {
      name: resp,
      to_email: correo,
      fecha_limite: startDate,
      lugar: area,
      qseb: tipo,
      categoria: cat,
      observacion: subCat,
      involucrados: involucrados.join(),
      pac: plan,
      registrado: creador,
    };

    emailjs
      .send("Gmail", apiKeys.TEMPLATE_ID, templateParams, apiKeys.USER_ID)
      .then(
        function (response) {
          console.log("SUCCESS!", response.status, response.text);
        },
        function (error) {
          console.log("FAILED...", error);
        }
      );
  }

  const iniciar = () => {
    var priceEls = document.querySelectorAll(".item");
    for (var i = 0; i < priceEls.length; i++) {
      var ele = priceEls[i].innerText;
      if (ele === "Usuario") {
        priceEls[i].click();
        break;
      }
    }
  };

  useEffect(() => {
    if (!data) {
      sacardata();
    }
    if (data) {
      setLoading(false);
    }
    if (tipo) {
      setCatData(
        data.filter((item) => {
          return item.Tipo === tipo;
        })
      );
    }
    if (cat) {
      setSubCatData(
        data2.filter((item) => {
          return item.Categoria === cat;
        })
      );
    }
    if (tipo && cat && subCat && involucrados.length !== 0) {
      setQue(true);
    } else {
      setQue(false);
    }
    if (area) {
      setDonde(true);
    } else {
      setDonde(false);
    }
    if (plan && resp && startDate) {
      setPacStep(true);
    } else {
      setPacStep(false);
    }
    if (
      tipo &&
      cat &&
      subCat &&
      involucrados.length !== 0 &&
      area &&
      plan &&
      resp &&
      startDate
    ) {
      setGuardar(true);
    } else {
      setGuardar(false);
    }
  }, [
    data,
    tipo,
    cat,
    subCat,
    involucrados,
    area,
    plan,
    resp,
    startDate,
    data2,
  ]);

  return (
    <div>
      {data && (
        <Segment>
          {data.map((item) => (
            <Segment>
              <Header>{item.Tipo}</Header>
              <Form>
                <TextArea value={item.Categoria}></TextArea>
                <Header content={item.src}></Header>
                <Input
                  id="anexoImg"
                  fluid
                  className="input-image"
                  type="file"
                  onChange={handleImageUpload}
                />
                <br />

                <Button
                  onClick={(e) => {
                    registrar(item.Tipo, item.Categoria);
                  }}
                >
                  SAVE
                </Button>
                <Image size="large" id="blah" src={previewImg} alt="Anexo" />
              </Form>
            </Segment>
          ))}
        </Segment>
      )}
    </div>
  );
}

export default Categorias;
